import React from 'react';
import './App.css';
import { Button, Checkbox, Form, Rate } from 'antd';
import 'antd/dist/antd.css';

const RateCats = () => {

  const onSubmit = (values: any[]) => {
    console.log('Received values of form: ', values);
  };

  const listCats = () => {
    return [1,2,3,4,5].map((i: number) => {
      const imgUrl = process.env.PUBLIC_URL + `/Cat${i}.jpg`;
      return (
        <Form.Item key={i} style={{textAlign: 'left'}}>
          {/* <div style={{display: 'flex', flexDirection: 'column', margin: '8px 8px', textAlign: 'left'}}> */}
          <img key={i} src={imgUrl} alt={`Cat${i}`} style={{width: '480px', marginBottom: '8px'}} />
          <Form.Item name={`cat${i}`} label={'Rating:'}
            rules={[
              {
                required: true,
                message: 'Please rate this cat!',
              },
            ]}
          >
            <Rate/>
          </Form.Item>
          {/* </div> */}
        </Form.Item>
      );
    });
  }

  return (
    <>
      <header className="App-header">
        <p>Rate some cats!</p>
      </header>
      <Form
        name="rating-cats"
        className="rating-form"
        initialValues={{
          subscribe: false,
        }}
        onFinish={onSubmit}
      >
        {listCats()}
        <Form.Item style={{textAlign: 'left'}}>
          <Form.Item name="subscribe" valuePropName="checked" noStyle>
            <Checkbox>Subscribe to our mailing list to get updates to your email inbox.</Checkbox>
          </Form.Item>
        </Form.Item>
        <Form.Item style={{textAlign: 'left'}}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Form.Item>
      </Form>
    </>
  );
}

export default RateCats;
